import { Paper } from "@material-ui/core";
import { DataGrid } from "@material-ui/data-grid";
import { useSnackbar } from "notistack";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getToken } from "../../../utils/mng-token";
import { setPreloadCertificates } from "./redux";

const columns = [
  { field: "id", headerName: "#", width: 50, type: "string" },
  { field: "name", headerName: "H v tn", width: 200, type: "string" },
  { field: "birthday", headerName: "Ngy sinh", width: 150, type: "string" },
  { field: "gender", headerName: "Gii tnh", type: "string" },
  { field: "university", headerName: "Trng", width: 250, type: "string" },
  { field: "faculty", headerName: "Ngnh hc", width: 200, type: "string" },
  { field: "degree", headerName: "Loi bng", width: 125, type: "string" },
  { field: "gradyear", headerName: "Nm tt nghip", width: 125, type: "string" },
  { field: "level", headerName: "Xp loi", width: 125, type: "string" },
  { field: "eduform", headerName: "Hnh thc o to", width: 200, type: "string" },
  { field: "issuelocation", headerName: "Ni cp", width: 125, type: "string" },
  { field: "issuedate", headerName: "Ngy cp", width: 200, type: "string" },
  { field: "headmaster", headerName: "Hiu trng", width: 200, type: "string" },
  { field: "regisno", headerName: "S hiu", width: 200, type: "string" },
  { field: "globalregisno", headerName: "S hiu vo s", width: 200, type: "string" },
];

export default function UploadedCertificateTable(props) {
  const fetching = useSelector((state) => state.certificateSlice.fetching);
  const certificates = useSelector((state) => state.certificateSlice.certificates);
  const dp = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    fetchUploadedCertificate();
  }, []);

  async function fetchUploadedCertificate() {
    const response = await fetch(`${process.env.REACT_APP_SERVER_URL}/staff/certificates`, {
      headers: { Authorization: getToken() },
    });
    const result = await response.json();
    if (!response.ok) {
      enqueueSnackbar("Fail to load uploaded certificates!: " + JSON.stringify(result), {
        variant: "error",
        anchorOrigin: { vertical: "top", horizontal: "center" },
      });
    } else {
      dp(setPreloadCertificates(result));
    }
  }

  return (
    <Paper style={{ height: "350px", width: "100%" }}>
      {fetching ? null : <DataGrid rows={certificates} columns={columns} autoPageSize rowHeight={48} loading={fetching} />}
    </Paper>
  );
}
